	-webkit-animation: show-hide 1.5s ease-in-out 1 forwards;
	        animation: show-hide 1.5s ease-in-out 1 forwards;
	font-style: italic;
}

.fail-mgs {
	-webkit-animation: show-hide 1.5s ease-in-out 1 forwards;
	        animation: show-hide 1.5s ease-in-out 1 forwards;
	font-style: italic;
}

@-webkit-keyframes show-hide {
	0% {
		opacity: 0;
	}
	50% {
		opacity: 1;
	}
	75% {
		opacity: 1;
	}
	100% {
		opacity: 0;
	}
}

@keyframes show-hide {
	0% {
		opacity: 0;
	}
	50% {
		opacity: 1;
	}
	75% {
		opacity: 1;
	}
	100% {
		opacity: 0;
	}
}

