  transition: all 0.2s ease-in-out;
  background-color: #333a45;
  width: 100%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  color: #eceef1;
  font-size: 1.5em;
}

.current-order {
  font-family: "Chakra Petch", sans-serif;
  font-size: 3rem;
  color: white;
  text-shadow: 2px 2px 4px #000000;
}

